<template>
  <div>
    <dashboard>
      <template v-slot:page_title>
        {{page_title}}
      </template>
      <!-- Content -->
      <template v-slot:dashboard_content>
        <div class="row">
          <div class="col-12 col-xl-6">
          </div>
        </div>
      </template>
    </dashboard>
  </div>
</template>

<script>
import Dashboard from '@/views/Dashboard'

export default {
  data () {
    return {
      page_title: 'Docentes',
      student_path: '/dashboard/student/',
      columns: [
        {
          label: 'Nucleo',
          field: 'nombre',
          sort: 'asc'
        },
        {
          label: 'Direccin',
          field: 'direccion',
          sort: 'asc'
        },
        {
          label: 'Telfono',
          field: 'telefono',
          sort: 'asc'
        }
      ],
      rows: []
    }
  },
  components: {
    'dashboard': Dashboard
  }
}
</script>



